import idbs from '../../../services/indexedDBService.js';

function initialState () {
    return {
        name: '',
        url: null,
        text: '',
        code: [],
        author: null,
        version: null,
        created: null
    };
};

const namespaced = true;

const state = initialState();

const getters = {
    nameAvailable: (state, getters, rootState, rootGetters) => async (name) => {
        if (!name || name === '') {
            return false;
        } else if (rootGetters['pattern/list/all'] === []) {
            return true;
        }

        const foundName = rootGetters['pattern/list/all'].find((pattern) => {
            return pattern.name === name;
        });

        if (foundName) {
            return false;
        } else {
            return true;
        }
    },

    pattern: (state) => {
        return state;
    }
};

const actions = {
    async setPattern ({ commit }, payload) {
        return commit('SET_PATTERN', payload);
    },

    async setName ({ commit }, payload) {
        const data = {
            name: payload.name,
            url: payload.name ? payload.name.replace(/ /g, '-') : null
        };

        return commit('SET_NAME', data);
    },

    async setContent ({ commit }, payload) {
        return commit('SET_CONTENT', payload);
    },

    async saveNew ({ commit }) {
        const data = {
            name: state.name,
            url: state.url,
            text: state.text,
            code: state.code,
            author: state.author,
            version: '0.1.0',
            created: (new Date()).toJSON()
        };

        await idbs.add('patterns', data);
        commit('RESET_PATTERN');
        return data.url;
    },

    async saveActive ({ dispatch }) {
        let versionArray = state.version.split('.').map(Number);
        console.log('versionArray 1', versionArray);
        versionArray.splice(2, 1, versionArray[2] + 1);
        const versionString = versionArray.join('.');

        console.log('versionArray 2', versionArray);
        console.log('versionString', versionString);

        const data = {
            name: state.name,
            url: state.url,
            text: state.text,
            code: state.code,
            author: state.author,
            version: versionString,
            created: (new Date()).toJSON()
        };

        await idbs.add('patterns', data);
        return dispatch('pattern/active/setPattern', data, { root: true });
    },

    async resetPattern ({ commit }) {
        try {
            commit('RESET_PATTERN');
        } catch (error) {
            console.error('store resetPattern error', error);
        }
    },

    async deletePattern ({ commit }) {
        try {
            console.log('new deletePattern');
            commit('RESET_PATTERN');
        } catch (error) {
            console.error('store deletePattern error', error);
        }
    }
};

const mutations = {
    SET_PATTERN: (state, payload) => {
        state.name = payload.name;
        state.url = payload.url;
        state.text = payload.text;
        state.code = payload.code;
        state.author = payload.author;
        state.version = payload.version;
        state.created = payload.created;
    },

    SET_NAME: (state, payload) => {
        state.name = payload.name;
        state.url = payload.url;
    },

    SET_CONTENT: (state, payload) => {
        state.text = payload.text;
        state.code = payload.code;
    },

    RESET_PATTERN: (state) => {
        // acquire initial state
        const s = initialState();
        Object.keys(s).forEach((key) => {
            state[key] = s[key];
        });
    }
};

export default {
    namespaced,
    state,
    getters,
    mutations,
    actions
};
