const namespaced = true;

const state = {
    display: false,
    type: {
        option: false,
        save: false,
        rename: false,
        delete: false
    }
};

const getters = {
    display: (state) => {
        return state.display;
    },

    typeDelete: (state) => {
        return state.type.delete;
    }
};

const actions = {
    async openDelete ({ commit }) {
        return commit('OPEN_DELETE');
    },

    async close ({ commit }) {
        return commit('CLOSE');
    }
};

const mutations = {
    OPEN_DELETE: (state) => {
        state.display = true;
        state.type.delete = true;
    },

    CLOSE: (state) => {
        state.display = false;
        state.type.option = false;
        state.type.save = false;
        state.type.rename = false;
        state.type.delete = false;
    }
};

export default {
    namespaced,
    state,
    getters,
    mutations,
    actions
};
