import idbs from '../../../services/indexedDBService.js';

function initialState () {
    return {
        id: null,
        name: '',
        url: null,
        text: '',
        code: [],
        author: null,
        version: null,
        created: null
    };
};

const namespaced = true;

const state = initialState();

const getters = {
    pattern: (state) => {
        return state;
    }
};

const actions = {
    async setPattern ({ commit, dispatch }, payload) {
        try {
            let data = await idbs.getAllFromIndexCreated(
                'patterns',
                'url',
                payload.url
            );

            if (data !== null) {
                await dispatch('pattern/new/setPattern', data, { root: true });
                commit('SET_PATTERN', data);
            };
        } catch (error) {
            console.error('store setPattern error', error);
        }
    },

    async resetPattern ({ commit }) {
        try {
            commit('RESET_PATTERN');
        } catch (error) {
            console.error('store resetPattern error', error);
        }
    },

    async deletePattern ({ commit, dispatch }) {
        try {
            await idbs.remove('patterns', 'url', state.url);
            commit('RESET_PATTERN');
            return await dispatch('pattern/new/deletePattern', null, { root: true });
        } catch (error) {
            console.error('store deletePattern error', error);
        }
    }
};

const mutations = {
    SET_PATTERN: (state, payload) => {
        if (payload.id) {
            state.id = payload.id;
        }

        state.name = payload.name;
        state.url = payload.url;
        state.text = payload.text;
        state.code = payload.code;
        state.author = payload.author;
        state.version = payload.version;
        state.created = payload.created;
    },

    RESET_PATTERN: (state) => {
        // acquire initial state
        const s = initialState();
        Object.keys(s).forEach((key) => {
            state[key] = s[key];
        });
    }
};

export default {
    namespaced,
    state,
    getters,
    mutations,
    actions
};
